import React, { useState, useEffect } from 'react';
import { imgs, designs2022, designs2020, designs2019, designs2018, arts2022, arts2020 } from "../images.ts";
import PhotoAlbum from "react-photo-album";
import ImgsViewer from "react-images-viewer";

import '../App.css';

// Importing images
import usa from '../assets/flag-usa.png';
import brasil from '../assets/flag-brasil.png';

const Portfolio = () => {
    const [language, setLanguage] = useState(0); // 0 is english, 1 is portuguese

    {/*useEffect(() => {
        document.title = 'Laura Coura - Portfolio';
    });*/}

    // Lightbox variables
    const [index, setIndex] = useState(-1);
    const [indexDesign2020, setIndexDesign2020] = useState(-1);
    const [indexDesign2019, setIndexDesign2019] = useState(-1);
    const [indexDesign2018, setIndexDesign2018] = useState(-1);
    const [indexArt2022, setIndexArt2022] = useState(-1);
    const [indexArt2020, setIndexArt2020] = useState(-1);

    const getSlides = (images) => {
        return(
            images.map(({original, width, height}) => ({
                src: original,
                height: height,
            }))
        )
    }
    
    return(
        <div className="app">
            <header className="header">
                {
                (language === 0) ?
                    <div className="header-left">
                    <a href='/'>Home</a>
                    <a href='/#about'>About</a>
                    <a href='/portfolio'>Portfolio</a>
                    </div>
                :
                <div className="header-left">
                    <a href='/'>Incio</a>
                    <a href='/#about'>Sobre</a>
                    <a href='/portfolio'>Portfolio</a>
                    </div>
                }
                
                <div className="header-right">
                <a href='https://www.linkedin.com/in/laura-coura/'>LinkedIn</a>
                <a href='https://github.com/LauraCoura'>GitHub</a>

                <button onClick={() => {setLanguage(1)}} className='button-image'>
                    <picture className='language-image'>
                    <source media="(min-height: 20px)" srcset={brasil} className="language-image"/>
                    <img src={brasil} className="language-image"/>
                    </picture>
                </button>

                <button onClick={() => {setLanguage(0)}} className='button-image'>
                    <picture className='language-image'>
                    <source media="(min-height: 20px)" srcset={usa} className="language-image"/>
                    <img src={usa} className="language-image"/>
                    </picture>
                </button>
                </div>
            </header>

            <main className='main'>
                <h1>Portfolio</h1>

                    <div className='row-arrow'>
                        <div class="arrow-right"></div>
                        <h2>DESIGN</h2>
                    </div>

                    {/* - - - - - - - - - 2022 - - - - - - - - - */}
                    <div className='photo-album'>
                        <div className='row-year'>
                            <div className='horizontal-line'></div>
                            <h2>2022</h2>
                        </div>

                        <PhotoAlbum layout="rows" photos={designs2022} onClick={({ index }) => setIndex(index)}/>

                        <ImgsViewer
                            imgs={getSlides(designs2022)}
                            currImg={index}
                            isOpen={index >= 0}
                            onClickPrev={() => setIndex(index-1)}
                            onClickNext={() => setIndex(index+1)}
                            onClose={() => setIndex(-1)}
                            theme={
                                {container: {background: 'rgba(14, 12, 17, .95)'},
                                arrow: {fill: '#ffffff', background: '#CE2D4F'},
                                close: {fill: '#ffffff', background: '#CE2D4F'}
                                }}
                        />
                    </div>

                    {/* - - - - - - - - - 2020 - - - - - - - - - */}
                    <div className='photo-album'>
                        <div className='row-year'>
                            <div className='horizontal-line'></div>
                            <h2>2020</h2>
                        </div>

                        <PhotoAlbum layout="rows" photos={designs2020} onClick={({ index }) => setIndexDesign2020(index)}/>

                        <ImgsViewer
                            imgs={getSlides(designs2020)}
                            currImg={indexDesign2020}
                            isOpen={indexDesign2020 >= 0}
                            onClickPrev={() => setIndexDesign2020(indexDesign2020-1)}
                            onClickNext={() => setIndexDesign2020(indexDesign2020+1)}
                            onClose={() => setIndexDesign2020(-1)}
                            theme={
                                {container: {background: 'rgba(14, 12, 17, .95)'},
                                arrow: {fill: '#ffffff', background: '#CE2D4F'},
                                close: {fill: '#ffffff', background: '#CE2D4F'}
                                }}
                        />
                    </div>

                   {/* - - - - - - - - - 2019 - - - - - - - - - */}
                   <div className='photo-album'>
                        <div className='row-year'>
                            <div className='horizontal-line'></div>
                            <h2>2019</h2>
                        </div>

                        <PhotoAlbum layout="rows" photos={designs2019} onClick={({ index }) => setIndexDesign2019(index)}/>

                        <ImgsViewer
                            imgs={getSlides(designs2019)}
                            currImg={indexDesign2019}
                            isOpen={indexDesign2019 >= 0}
                            onClickPrev={() => setIndexDesign2019(indexDesign2019-1)}
                            onClickNext={() => setIndexDesign2019(indexDesign2019+1)}
                            onClose={() => setIndexDesign2019(-1)}
                            theme={
                                {container: {background: 'rgba(14, 12, 17, .95)'},
                                arrow: {fill: '#ffffff', background: '#CE2D4F'},
                                close: {fill: '#ffffff', background: '#CE2D4F'}
                                }}
                        />
                    </div>

                    {/* - - - - - - - - - 2018 - - - - - - - - - */}
                   <div className='photo-album'>
                        <div className='row-year'>
                            <div className='horizontal-line'></div>
                            <h2>2018</h2>
                        </div>

                        <PhotoAlbum layout="rows" photos={designs2018} onClick={({ index }) => setIndexDesign2018(index)}/>

                        <ImgsViewer
                            imgs={getSlides(designs2018)}
                            currImg={indexDesign2018}
                            isOpen={indexDesign2018 >= 0}
                            onClickPrev={() => setIndexDesign2018(indexDesign2018-1)}
                            onClickNext={() => setIndexDesign2018(indexDesign2018+1)}
                            onClose={() => setIndexDesign2018(-1)}
                            theme={
                                {container: {background: 'rgba(14, 12, 17, .95)'},
                                arrow: {fill: '#ffffff', background: '#CE2D4F'},
                                close: {fill: '#ffffff', background: '#CE2D4F'}
                                }}
                        />
                    </div>

                    <div className='row-arrow'>
                        <div class="arrow-right"></div>
                        { (language === 0) ?
                            <h2>ART</h2>
                            :
                            <h2>ARTE</h2>
                        }
                    </div>

                    {/* - - - - - - - - - 2022 - - - - - - - - - */}
                   <div className='photo-album'>
                        <div className='row-year'>
                            <div className='horizontal-line'></div>
                            <h2>2022</h2>
                        </div>

                        <PhotoAlbum layout="rows" photos={arts2022} onClick={({ index }) => setIndexArt2022(index)}/>

                        <ImgsViewer
                            imgs={getSlides(arts2022)}
                            currImg={indexArt2022}
                            isOpen={indexArt2022 >= 0}
                            onClickPrev={() => setIndexArt2022(indexArt2022-1)}
                            onClickNext={() => setIndexArt2022(indexArt2022+1)}
                            onClose={() => setIndexArt2022(-1)}
                            theme={
                                {container: {background: 'rgba(14, 12, 17, .95)'},
                                arrow: {fill: '#ffffff', background: '#CE2D4F'},
                                close: {fill: '#ffffff', background: '#CE2D4F'}
                                }}
                        />
                    </div>

                    {/* - - - - - - - - - 2020 - - - - - - - - - */}
                   <div className='photo-album'>
                        <div className='row-year'>
                            <div className='horizontal-line'></div>
                            <h2>2020</h2>
                        </div>

                        <PhotoAlbum layout="rows" photos={arts2020} onClick={({ index }) => setIndexArt2020(index)}/>

                        <ImgsViewer
                            imgs={getSlides(arts2020)}
                            currImg={indexArt2020}
                            isOpen={indexArt2020 >= 0}
                            onClickPrev={() => setIndexArt2020(indexArt2020-1)}
                            onClickNext={() => setIndexArt2020(indexArt2020+1)}
                            onClose={() => setIndexArt2020(-1)}
                            theme={
                                {container: {background: 'rgba(14, 12, 17, .95)'},
                                arrow: {fill: '#ffffff', background: '#CE2D4F'},
                                close: {fill: '#ffffff', background: '#CE2D4F'}
                                }}
                        />
                    </div>
            </main>
        </div>
    );
}

export default Portfolio;